import { Card ,CardContent,Typography} from '@material-ui/core'
import "./InfoBox.css"
import React from 'react'
import { prettyPrintStat } from './util';

export default function InfoBox({active,title,cases,total,onClick,isRed}) {
    return (
      <Card
      onClick={onClick}
      className={`infoBox ${active && "infoBox--selected"} ${
        isRed && "infoBox--red" 
      }`}
      >
        <CardContent>
          {/* Title */}
          <Typography className="infoBox__title" variant="h6">{title}</Typography>
          {/* cases */}
          <h2 className='infoBox__cases '>{prettyPrintStat(cases)}</h2>
          {/* Total */}
          <Typography className="info__total">{prettyPrintStat(total)}</Typography>
        </CardContent>
      </Card>
    );
}
