import React,{useEffect,useState} from 'react'
import {GridList,GridListTile} from "@material-ui/core"
import "./News.css"

function News() {
    const [newsList, setNewsList] = useState([]);
    let i=5;
    // const [newList, setNewList] = useState([1,2,3,4,4,4,4,4,4,4,4,4,4,4,3,2,32332,,3,2,32,23,2,3])
useEffect(() => {
    fetch("https://newsapi.org/v2/everything?q=corona&sortBy=popularity&apiKey=4eb5637541504734bc95a3a11c9634f4")
    .then(response=>response.json())
    .then(data=>{
        setNewsList(data["articles"]);
    })
    .catch(err=>console.log(err))
}, [])

    if(newsList.length==0)
    return (
        <div>
            
        </div>
    )
    else
    return (
    <ul className="news__list">
        {newsList.map((news)=><li>{news["title"]} <a target="_blank" href={news["url"]}>read more...</a></li>)}
    </ul>
    )
}

export default News
