import React, { useEffect, useState } from "react";
import "./App.css";
import {
  MenuItem,
  FormControl,
  Select,
  Card,
  CardContent,
} from "@material-ui/core";
import InfoBox from "./InfoBox";
import "leaflet/dist/leaflet.css"
import News from "./News"
import Map from "./Map"
import Table from "./Table"
import {sortData} from "./util"
import LineGraph from "./LineGraph"



function App() {
  const [countries, setCountries] = useState([]);
  const [country, setCountry] = useState("worldwide");
  const [countryInfo,setCountryInfo]=useState(" ");
  const [tableData,setTableData]=useState([]);
  const [mapCenter,setMapCenter]=useState([20,-10])
  const [mapZoom, setMapZoom] = useState(2)
  const [mapCountries, setMapCountries] = useState([])
  const [casesType, setCasesType] = useState("cases")

  useEffect(()=>{
    fetch("https://disease.sh/v3/covid-19/all")
    .then(response=>response.json())
    .then(data=>{setCountryInfo(data)})
    
  },[])

  useEffect(() => {
    const getCountriesData = async () => {
      await fetch("https://disease.sh/v3/covid-19/countries")
        .then((response) => response.json())
        .then((data) => {
          const countries = data.map((country) => ({
            name: country.country,
            value: country.countryInfo.iso2,
          }));
          const sortedData=sortData(data)
          setCountries(countries);
          setTableData(sortedData);
          setMapCountries(data)
          // console.log(typeof sortedData)
          // console.log(sortedData)
        });
    };
    getCountriesData();
  }, []);

  const onCountryChange = async (event) => {
    const countryCode = event.target.value;
    setCountry(countryCode);


    const url =
      countryCode === "worldwide"
        ? "https://disease.sh/v3/covid-19/all"
        : `https://disease.sh/v3/covid-19/countries/${countryCode}`;

    await fetch(url)
    .then(response=>response.json())
    .then(data=>{
      setCountryInfo(data)
      if(countryCode==="worldwide"){
        setMapCenter([20,-10]);
        setMapZoom(2);
        return;
      }
      setMapZoom(4)
      setMapCenter([data.countryInfo.lat,data.countryInfo.long])
      // console.log(data.countryInfo.long)
    })
  };

  

  return (
    <div className="app">

<div className="app__header">
        {/* Header */}
          <h1 className="title">COVID-19-TRACKER</h1>
          <FormControl className="app__dropdown">
            <Select
              variant="outlined"
              value={country}
              onChange={onCountryChange}
            >
              <MenuItem value="worldwide">Worldwide</MenuItem>
              {countries.map((country) => {
                return (
                  <MenuItem value={country.value}>{country.name}</MenuItem>
                );
              })}
            </Select>
          </FormControl>
        </div>
<div className="content">
  {/* Stats */}
  <div className="app__stats app__left">
          <InfoBox
            isRed
            active={casesType=="cases"}
            title="Coronavirus Cases"
            total={countryInfo.cases}
            cases={countryInfo.todayCases}
            onClick={e=>setCasesType('cases')}
          />

          <InfoBox
            active={casesType=="recovered"}
            title="Recovered"
            total={countryInfo.recovered}
            cases={countryInfo.todayRecovered}
            onClick={e=>setCasesType('recovered')}
          />

          <InfoBox
            isRed
            active={casesType=="deaths"}
            title="Deaths"
            total={countryInfo.deaths}
            cases={countryInfo.todayDeaths}
            onClick={e=>setCasesType('deaths')}
          />
        </div>
  <div className="app__mid">
        {/* News Displai */}
        <h2 class="news">News</h2>
        <News/>
        {/* Map Component */}
        <Map countries={mapCountries} center={mapCenter}
         zoom={mapZoom} casesType={casesType}/>
      </div>    
  <Card className="app__right">
        <CardContent>
          <div class="app__information">
            {/* Table */}
            <h3>Lives Cases by Country</h3>
            <Table countries={tableData} />
            {/* Graph */}
            <h3>Worldwide new {casesType}</h3>
            <LineGraph casesType={casesType}/>
          </div>
        </CardContent>
  </Card>
    </div>
    </div>
  );
}

export default App;
